:root { 
  --black: #000000;
  --dove-gray: #6f6f6f;
  --gray: #919191;
  --mist-gray: #c4c4c4;
  --storm-dust: #626262;
  --thunder: #332f2f;
  --white: #ffffff;
 
  --font-size-l: 15px;
  --font-size-m: 10px;
  --font-size-s: 6px;
  --font-size-xl: 16px;
  --font-size-xxl: 30px;
 
  --font-family-inter: "Inter", Helvetica;
}
.inter-normal-gray-10px {
  color: var(--gray);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}

.inter-normal-gray-16px {
  color: var(--gray);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-xl);
  font-style: normal;
  font-weight: 400;
}

.inter-normal-white-10px {
  color: var(--white);
  font-family: var(--font-family-inter);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}
